html,
body {
  background-color: var(--background);
  color: var(--foreground);
  transition: 0.30s all ease;
}

html {
  font-size: 16px;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
  font-size: 1rem;
  line-height: 1.5;
  margin: 0;
  padding: 0;
  min-height: 100%;
  position: relative;
  overflow-x: hidden;
  font-family: "Inter", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
}

*,
*:before,
*:after {
  box-sizing: inherit;
  text-rendering: geometricPrecision;
  -webkit-tap-highlight-color: transparent;
}

p,
small {
  font-weight: 400;
  color: inherit;
  letter-spacing: -0.005625rem;
  font-family: "Inter", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
}

p {
  margin: 1rem 0;
  font-size: 1em;
  line-height: 1.625em;
}

small {
  margin: 0;
  line-height: 1.5;
  font-size: 0.875rem;
}

b {
  font-weight: 600;
}

span {
  font-size: inherit;
  color: inherit;
  font-weight: inherit;
}

img {
  max-width: 100%;
}

a {
  cursor: pointer;
  font-size: inherit;
  -webkit-touch-callout: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-box-align: center;
  align-items: center;
  color: var(--link);
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul,
ol {
  padding: 0;
  list-style-type: none;
  margin: 8pt 8pt 8pt 16pt;
  color: var(--foreground);
}

ol {
  list-style-type: decimal;
}

li {
  margin-bottom: 0.625rem;
  font-size: 1em;
  line-height: 1.625em;
}

ul li:before {
  content: '–';
  display: inline-block;
  color: var(--accents_4);
  position: absolute;
  margin-left: -0.9375rem;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: inherit;
  margin: 0 0 0.625rem 0;
}

h1 {
  font-size: 3rem;
  letter-spacing: -0.066875rem;
  line-height: 1.5;
  font-weight: 700;
}

h2 {
  font-size: 2.25rem;
  letter-spacing: -0.020625rem;
  font-weight: 600;
}

h3 {
  font-size: 1.5rem;
  letter-spacing: -0.029375rem;
  font-weight: 600;
}

h4 {
  font-size: 1.25rem;
  letter-spacing: -0.020625rem;
  font-weight: 600;
}

h5 {
  font-size: 1rem;
  letter-spacing: -0.01125rem;
  font-weight: 600;
}

h6 {
  font-size: 0.875rem;
  letter-spacing: -0.005625rem;
  font-weight: 600;
}

button,
input,
select,
textarea {
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
  color: inherit;
  margin: 0;
}

button:focus,
input:focus,
select:focus,
textarea:focus {
  outline: none;
}

pre {
  background-color: transparent;
  border: 1px solid var(--accents_2);
  border-radius: 5px;
  padding: calc(0.75 * 16pt) 16pt;
  margin: 16pt 0;
  font-family: "Menlo", Monaco, Lucida Console, Liberation Mono, DejaVu Sans Mono, Bitstream Vera Sans Mono, Courier New, monospace;
  white-space: pre;
  overflow: auto;
  line-height: 1.5;
  text-align: left;
  font-size: 0.8125rem;
  -webkit-overflow-scrolling: touch;
  position: relative;
}

pre span, pre p {
  font-size: inherit;
  color: inherit;
  margin: 0;
}

pre code {
  color: var(--foreground);
  font-size: 0.8125rem;
  line-height: 1.25rem;
  white-space: pre;
}

pre code:before, pre code:after {
  display: none;
}

pre::-webkit-scrollbar {
  display: none;
  width: 0;
  height: 0;
  background: transparent;
}

code {
  color: var(--code);
  font-family: "Menlo", Monaco, Lucida Console, Liberation Mono, DejaVu Sans Mono, Bitstream Vera Sans Mono, Courier New, monospace;
  font-size: 0.9em;
  white-space: pre-wrap;
}

code:before, code:after {
  content: '\`';
}

hr {
  border-color: var(--accents_2);
}

details {
  border: none;
  padding-left: 5px;
  padding-top: 10px;
  padding-bottom: 10px;
  border-radius: 5px;
}

details:focus,
details:hover,
details:active {
  outline: none;
}

summary {
  cursor: pointer;
  user-select: none;
  font-weight: bold;
  outline: none;
}

summary::-webkit-details-marker,
summary::before {
  display: none;
}

summary::-moz-list-bullet {
  font-size: 0;
}

summary:focus,
summary:hover,
summary:active {
  outline: none;
}

details[open] > div {
  animation: fade-in 0.5s forwards;
}

@keyframes fade-in {
  0% {
    transform: translateY(-20px);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

blockquote {
  padding: calc(0.667 * 16pt) 16pt;
  color: var(--accents_5);
  background-color: var(--accents_1);
  border-radius: 5px;
  margin: 1.5rem 0;
  border: 1px solid var(--border);
  overflow-x: auto;
}

blockquote :global(*:first-child) {
  margin-top: 0;
}

blockquote :global(*:last-child) {
  margin-bottom: 0;
}

::selection {
  background-color: var(--selection);
  color: var(--foreground);
}

table {
  border-collapse: separate;
  border-spacing: 0;
  width: 100%;
}

table thead th td {
  height: 2.5rem;
}

table tbody tr td {
  height: 3.125rem;
}

table th, table td {
  padding: 0 0.625rem;
  text-align: left;
}

table th {
  height: 2.5rem;
  color: var(--accents_5);
  font-size: 0.875rem;
  font-weight: 400;
  letter-spacing: 0;
  background: var(--accents_1);
  border-bottom: 1px solid var(--accents_2);
  border-top: 1px solid var(--accents_2);
}

table th:nth-child(1) {
  border-bottom: 1px solid var(--accents_2);
  border-left: 1px solid var(--accents_2);
  border-radius: 4px 0 0 4px;
  border-top: 1px solid var(--accents_2);
}

table th:last-child {
  border-bottom: 1px solid var(--accents_2);
  border-radius: 0 4px 4px 0;
  border-right: 1px solid var(--accents_2);
  border-top: 1px solid var(--accents_2);
}

table tr td {
  color: var(--accents_6);
  font-size: 0.875rem;
  height: 2.5rem;
}

table td:nth-child(1) {
  border-left: 1px solid transparent;
}

